import { Component, OnInit } from '@angular/core';
import {Movie} from '../../model/movie';
import{movieList} from '../../model/movieList';
import{MovieService} from '../../services/movie.service';
@Component({
  selector: 'app-movies',
  templateUrl: './movies.component.html',
  styleUrls: ['./movies.component.css']
})
export class MoviesComponent implements OnInit {

  movie: Movie ={
    id:1,
    name: "Star wars",
    year : 1997
  }

/*   movies = movieList; */
movies: Movie[];

  constructor(private movieService:MovieService) { }

  getMovieFromService() : void{
   // this.movies = this.movieService.getMovie();
   this.movieService.getMovie2().subscribe(
    (updatedMovie) => {
      this.movies = updatedMovie;
      console.log(`this.movies  = ${JSON.stringify(this.movies )}`);
    }
   );
  }



  ngOnInit() {

    this.getMovieFromService();
  }

  selectedMovie: Movie;
  onSelect(movie:Movie) : void {
    this.selectedMovie = movie;
    console.log(`seletedMovie = ${JSON.stringify(this.selectedMovie)}`);
   /*  alert(`seletedMovie = ${JSON.stringify(this.selectedMovie)}`); */
  }

}
